import { Component, OnInit } from '@angular/core';
import { NavController, ModalController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { FirstPage } from '../first/first';
import { DataService } from '../../providers/data.service';
import { Cache } from '../../cache/cache.global';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
  styles: ['home.scss'],
  providers: [DataService, Cache]
})
export class HomePage implements OnInit {

	/**
   * 轮播列表
   * 
   * @type {*}
   * @memberof HomePage
   */
  sliders: any;
  /**
   * 分类项目
   * 
   * @type {*}
   * @memberof HomePage
   */
  categories: any;
  /**
   * 热销商品
   * 
   * @type {*}
   * @memberof HomePage
   */
  hotSail: any;

	/**
	 * Creates an instance of HomePage.
	 * @param {Cache} cache 
	 * @param {NavController} navCtrl 
	 * @param {DataService} dataService 
	 * @param {ModalController} modalCtrl 
	 * @memberof HomePage
	 */
  constructor(public cache: Cache,
    public navCtrl: NavController,
    public storage: Storage,
    private dataService: DataService,
    public modalCtrl: ModalController) {
  }

	/**
	 * 初始化
	 * 
	 * @memberof HomePage
	 */
  ngOnInit() {
    // this.storage.get('showFirst').then(val => {
    //   !val && this.presentModal();
    // })

    //获取轮播图片
    this.sliders = this.dataService.getSliders();
    //获取分类项目
    this.categories = this.dataService.getCategories();
    //获取热销商品
    this.hotSail = this.dataService.getHotSail();
  }

  ionViewDidEnter() {
    console.info('home')
  }

  /**
   * 跳转到列表
   * 
   * @param {any} category 
   * @memberof HomePage
   */
  goList(category) {
    // this.navCtrl.push(ListPage, {
    //   category: category
    // });
    this.cache.set('category', category.title);
    this.navCtrl.parent.select(1);
  }

	/**
	 * 模态窗体
	 * 
	 * @memberof HomePage
	 */
  presentModal() {
    this.storage.set('showFirst', true);
    let modal = this.modalCtrl.create(FirstPage);
    modal.present({
      animate: false,
      duration: 0
    });
  }


}



