import { Component } from '@angular/core';
import { NavController, ActionSheetController, ToastController, ModalController, NavParams } from 'ionic-angular';

import { Cache } from '../../cache/cache.global';
import { DetailPage } from '../detail/detail';
import { DataService } from '../../providers/data.service';

@Component({
  selector: 'page-list',
  templateUrl: 'list.html',
  providers: [DataService, Cache]

})
export class ListPage {

  /**
   * 所有分类
   * 
   * @type {*}
   * @memberof ListPage
   */
  categories: any;
  /**
   * 当前分类
   * 
   * @type {any}
   * @memberof ListPage
   */
  category: any;
  /**
   * 搜索结果
   * 
   * @type {*}
   * @memberof ListPage
   */
  searchItems: any;
  /**
   * 搜索分类
   * 
   * @type {*}
   * @memberof ListPage
   */
  searchName: any;
  /**
   * Creates an instance of ListPage.
   * @param {NavController} navCtrl 
   * @param {ActionSheetController} actionSheetCtrl 
   * @param {ToastController} toastCtrl 
   * @param {ModalController} modalCtrl 
   * @memberof ListPage
   */
  constructor(public navCtrl: NavController,
    public actionSheetCtrl: ActionSheetController,
    public toastCtrl: ToastController,
    private dataService: DataService,
    public cache: Cache,
    public navParams: NavParams,
    public modalCtrl: ModalController) {
    this.categories = this.dataService.getAll();
    // let name = navParams.get('category') && navParams.get('category').title;
    // name && this.setCategoty(name);
    // this.category = this.category || this.categories[0];
  }

  /**
   * 初始化
   * 
   * @memberof ListPage
   */
  ngOnInit() {
    this.category = this.categories[0];
  }

  /**
   * 显示时执行
   * 
   * @memberof ListPage
   */
  ionViewDidEnter() {
    let name = this.cache.get('category');
    name && this.setCategoty(name);
    this.category = this.category || this.categories[0];
  }

  /**
   * 设置分类
   * 
   * @param {any} name 
   * @memberof ListPage
   */
  setCategoty(name) {
    this.categories.forEach(category => {
      name.indexOf(category.name) > -1 &&
        (this.category = category);
    });
  }

  /**
   * 选择分类
   * 
   * @param {any} category 
   * @memberof ListPage
   */
  presentCategory(category, search?: boolean) {
    this.searchItems = [];
    this.searchName = search ? category.name : null;
    this.category = category;
  }

  /**
   * 搜索商品
   * 
   * @param {any} $event 
   * @memberof ListPage
   */
  searchCategory($event) {
    let name = $event.target.value;
    this.searchItems = this.categories.filter((category) => {
      return category.name.indexOf(name) > -1
    })
  }

  /**
   * 显示图片详情
   * 
   * @param {any} items 
   * @param {any} index 
   * @memberof ListPage
   */
  showDetail(items, index) {
    let modal = this.modalCtrl.create(DetailPage, {
      index: index,
      items: items
    });
    modal.present({
      animate: false,
      duration: 0
    });
  }

}



